* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  line-height: 18px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 3px;
  display: block;
}

.card__title {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  padding-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  display: flex;
}

.card__rate--description {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 16px;
  display: flex;
}

.card__price--label {
  color: #616070;
  padding-top: 0;
  padding-bottom: 0;
  font-size: 12px;
}

.card__price--value {
  color: #060b35;
  padding-top: 0;
  padding-bottom: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
